import React, { Component } from 'react';

class Media extends Component {
  constructor(props) {
    super(props);

    this.state = {
      alt: this.alt(),
      url: this.url(),
      src: this.src(),
    }
  }

  thumbnailIndex() {
    this.props.node.thumbnail_resources.forEach((item, index) => {
      if (item.config_width === 640) { return index }
    })

    return 4; // MAGIC
  }

  url() {
    return "https://www.instagram.com/p/" + this.props.node.shortcode
  }

  src() {
    switch (this.props.node.__typename) {
      case "GraphSidecar": return this.props.node.thumbnail_resources[this.thumbnailIndex()].src
      case "GraphVideo": return this.props.node.thumbnail_src
      default: return this.props.node.thumbnail_resources[this.thumbnailIndex()].src
    }
  }

  alt() {
    if (this.props.node.edge_media_to_caption.edges[0] && this.props.node.edge_media_to_caption.edges[0].node) {
      return this.props.node.edge_media_to_caption.edges[0].node.text
    }

    return escape(this.props.node.accessibility_caption)
  }

  render() {
    return (
      <a href={this.state.url} className="Media" rel='noopener' target='_blank'>
        <img className="Media" src={this.state.src} alt={this.state.alt}></img>
      </a>
    );
  }
}

export default Media;



