import React, { Component } from 'react';
import Media from './Media';
import './Feed.css';

class Feed extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isFetching: true,
      media: [],
    };

    const url = "https://www.instagram.com/" + props.username + "/"

    fetch(url)
      .then(resp => resp.text())
      .then(body => body.split("window._sharedData = ")[1].split("</script>")[0])
      .then(data => JSON.parse(data.substr(0, data.length - 1)))
      .then(json => { this.setState({ isFetching: false, media: json.entry_data.ProfilePage[0].graphql.user.edge_owner_to_timeline_media.edges }) })
      .catch(err => console.log(err))
  }

  render() {
    return (
      <div className="Feed">
        {this.state.media.map((media, index) => (
          <Media key={index} node={media.node} />
        ))}
      </div>
    );
  }
}

export default Feed;



